@import 'https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap';

$myFont: Roboto, sans-serif;
$colorButton: #00acdc;
$widthElemets: 200px;
$colorButtonWite: white;

* {
  box-sizing: border-box;
}

body,
p {
  margin: 0;
  padding: 0;
  font-family: $myFont;
}

.block {
  width: $widthElemets;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  display: flex;
  flex-direction: column;

  img {
    display: block;
    height: 134px;
    margin: 0 3px 40px;
  }

  &__apple {
    white-space: nowrap;
    width: 166px;
    font-size: 12px;
    color: #060b35;
    font-weight: 500;
    line-height: 18px;
  }

  &__product {
    font-size: 10px;
    color: #616070;
    margin-top: 4px;
    font-weight: 400px;
    line-height: 14px;
  }

  &__stars {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 16px;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    color: #060b35;
    font-weight: 400;
    line-height: 14px;
    margin-left: 17px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    width: 166px;
    margin-bottom: 16px;
  }

  &__price-txt {
    font-size: 12px;
    font-weight: 400;
    color: #616070;
    line-height: 18px;
  }

  &__costs {
    font-size: 16px;
    font-weight: 700;
    color: #060b35;
    line-height: 18px;
  }

  &__button {
    border: 1px solid $colorButton;
    background-color: $colorButton;
    width: 166px;
    height: 40px;
    border-radius: 5px;
    color: $colorButtonWite;
    font-weight: 700;
    font-size: 14px;
    text-decoration: none;
    line-height: 16px;
    align-items: center;
    text-transform: uppercase;
    text-align: center;
    display: flex;
    justify-content: center;

    &:hover {
      background-color: $colorButtonWite;
      color: $colorButton;
    }
  }
}

.stars {
  display: flex;
}

.stars__stars {
  width: 16px;
  height: 16px;
  background-position: center;
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
}

.stars__stars:not(:last-child) {
  margin-right: 4px;
}

.stars--4 .stars__stars:nth-child(-n + 4) {
  background-image: url(../images/star-active.svg);
}
